<template>
  <div>
    <div class="ccc">
      <!--    -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/Reader/Welcome' }"
          ></el-breadcrumb-item
        >
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>

      <!--    -->
      <el-card class="box-card">
        <div class="demo-basic--circle">
          <div class="block">
            <el-avatar :size="50" :src="circleUrl"></el-avatar>
          </div>
        </div>
        <div style="margin-bottom: 30px; margin-left: 10px">
          <h1></h1>
        </div>

        <el-form ref="form" :model="userForm" label-width="80px">
          <el-form-item label="">
            <el-input v-model="userForm.uName" disabled></el-input>
          </el-form-item>
          <el-form-item label="ID">
            <el-input v-model="userForm.uID" disabled></el-input>
          </el-form-item>
        </el-form>
        <div class="boxdiv">
          <el-button plain @click="showEditDialog"
            ></el-button
          >
        </div>
        <div class="boxdiv">
          <el-button plain @click="showEdit_Dialog"
            ></el-button
          >
        </div>
        <div class="boxdiv">
          <el-button plain @click="addDialogVisible = true"
            ></el-button
          >
        </div>
      </el-card>
    </div>
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="40%"
      @close="addDialogClosed"
    >
      <!--     -->
      <el-form
        :model="addForm"
        :rules="addRules"
        ref="addFormRef"
        label-width="120px"
      >
        <el-form-item label="" prop="bName">
          <el-input v-model="addForm.bName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bAuthor">
          <el-input v-model="addForm.bAuthor"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bYear">
          <el-input v-model="addForm.bYear"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bTotal">
          <el-input v-model="addForm.bTotal"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bReason">
          <el-radio-group v-model="addForm.bReason">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addBook"> </el-button>
      </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="editDialogVisible" width="40%">
      <!---->
      <el-form
        :model="editForm"
        :rules="editRules"
        ref="editFormRef"
        label-width="120px"
      >
        <el-form-item label="" prop="uName">
          <el-input v-model="editForm.uName" disabled></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="uID">
          <el-input v-model="editForm.uID" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="uPWD">
          <el-input v-model="editForm.uPWD"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ucheckPWD">
          <el-input v-model="editForm.ucheckPWD"></el-input>
        </el-form-item>
      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false" @close="editDialogClosed"
          > </el-button
        >
        <el-button type="primary" @click="editRecordInfo"> </el-button>
      </span>
    </el-dialog>

    <el-dialog
      title=""
      :visible.sync="edit_DialogVisible"
      width="40%"
    >
      <!---->
      <el-form
        :model="edit_Form"
        :rules="edit_Rules"
        ref="edit_FormRef"
        label-width="120px"
      >
        <el-form-item label="ID" prop="uID">
          <el-input v-model="edit_Form.uID" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="uName">
          <el-input v-model="edit_Form.uName"></el-input>
        </el-form-item>

        <el-form-item label="" prop="uPhone">
          <el-input v-model="edit_Form.uPhone"></el-input>
        </el-form-item>
      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
        <el-button
          @click="edit_DialogVisible = false"
          @close="edit_DialogClosed"
          > </el-button
        >
        <el-button type="primary" @click="edit_RecordInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "ReaderUsers",
  //
  data() {
    var u_checkPWD = (rule, value, callback) => {
      if (value !== this.editForm.uPWD)
        return callback(new Error(""));
      else return callback();
    };
    var checkbTotal = (rule, value, callback) => {
      const regbTotal = /^[0-9]{1,3}$/;
      if (regbTotal.test(value)) return callback();
      else callback(new Error(""));
    };
    var checkbYear = (rule, value, callback) => {
      const regbYear = /^[1-2][0-9]{3}$/;
      if (regbYear.test(value)) return callback();
      else callback(new Error(""));
    };
    var checkuID = (rule, value, callback) => {
      const reguID = /^[1-2][0-9]{7}$/;
      if (reguID.test(value)) return callback();
      else callback(new Error("/"));
    };
    var checkuPhone = (rule, value, callback) => {
      const regPhone = /^1[3-9][0-9]{9}$/;
      if (regPhone.test(value)) return callback();
      else callback(new Error(""));
    };
    return {
      ///
      circleUrl:
        "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png",
      userForm: {
        uName: "",
        uID: "",
      },

      addDialogVisible: false, //
      addForm: {
        bName: "",
        bAuthor: "",
        bTotal: 0,
        bReason: "",
      },
      addRules: {
        bName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 1,
            max: 20,
            message: " 1  20 ",
            trigger: "blur",
          },
        ],
        bAuthor: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 20,
            message: " 2  20 ",
            trigger: "blur",
          },
        ],
        bYear: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkbYear, trigger: "blur" },
        ],

        bTotal: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkbTotal, trigger: "blur" },
        ],
        bReason: [
          { required: true, message: "", trigger: "blur" },
        ],
      },

      editDialogVisible: false, //,rID
      editForm: {
        uName: "",
        uID: "",
        uPWD: "",
        ucheckPWD: "",
      },
      editRules: {
        uPWD: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 15, message: " 6 15 ", trigger: "blur" },
        ],
        ucheckPWD: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 15, message: " 6 15 ", trigger: "blur" },
          { validator: u_checkPWD, trigger: "blur" },
        ],
      },
      //

      edit_DialogVisible: false, //,rID
      edit_Form: {
        uName: "",
        uID: "",
        uPhone: "",
      },
      edit_Rules: {
        uName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 10,
            message: " 2  10 ",
            trigger: "blur",
          },
        ],
        uID: [
          { message: "/", trigger: "blur" },
          { validator: checkuID, trigger: "blur" },
        ],
        uPhone: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkuPhone, trigger: "blur" },
        ],
      },
    };
  },

  created() {
    this.getNowUser();
  }, //*/
  methods: {
    getNowUser() {
      var tmpuname = window.sessionStorage.getItem("uName");
      var tmpuid = window.sessionStorage.getItem("uID");
      this.userForm.uName = JSON.parse(tmpuname);
      this.userForm.uID = JSON.parse(tmpuid);
      this.editForm.uName = JSON.parse(tmpuname);
      this.editForm.uID = JSON.parse(tmpuid);
      this.edit_Form.uName = JSON.parse(tmpuname);
      this.edit_Form.uID = JSON.parse(tmpuid);
    }, //

    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    }, //
    async addBook() {
      this.$refs.addFormRef.validate(
        //aync
        async (valid) => {
          if (!valid) return;
          //
          else {
            await new Promise((resolve) => {
              this.$http
                .post("/users/buyBooks/", JSON.stringify(this.addForm))
                .then((res) => {
                  //addForm
                  //rID
                  console.log(res);
                  if (res.data.success === false)
                    this.$message.error(res.data.message);
                  else this.$message.success("");

                  this.addDialogVisible = false;
                });
              resolve();
            });
          }
        }
      );
    }, //

    showEditDialog() {
      var tmpuname = window.sessionStorage.getItem("uName");
      var tmpuid = window.sessionStorage.getItem("uID");
      this.userForm.uName = JSON.parse(tmpuname);
      this.userForm.uID = JSON.parse(tmpuid);

      this.editDialogVisible = true;
    }, //
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    }, //
    editRecordInfo() {
      this.$refs.editFormRef.validate(
        //aync
        async (valid) => {
          if (!valid) return;
          //
          else {
            await new Promise((resolve) => {
              this.$http
                .post("/users/uPWD/", JSON.stringify(this.editForm))
                .then((res) => {
                  if (res.data.success === false)
                    this.$message.error(res.data.message);
                  else this.$message.success("");
                  this.editDialogVisible = false;
                  this.getNowUser();
                });
              resolve();
            });
          }
        }
      );
    }, //
    showEdit_Dialog() {
      var tmpuname = window.sessionStorage.getItem("uName");
      var tmpuid = window.sessionStorage.getItem("uID");
      this.userForm.uName = JSON.parse(tmpuname);
      this.userForm.uID = JSON.parse(tmpuid);

      this.edit_DialogVisible = true;
    }, //
    edit_DialogClosed() {
      this.$refs.edit_FormRef.resetFields();
    }, //
    edit_RecordInfo() {
      this.$refs.edit_FormRef.validate(
        //aync
        async (valid) => {
          if (!valid) return;
          //
          else {
            console.log(valid);
            await new Promise((resolve) => {
              this.$http
                .post("/users/uInform/", JSON.stringify(this.edit_Form))
                .then((res) => {
                  if (res.data.success === false)
                    this.$message.error(res.data.message);
                  else this.$message.success("");
                  this.edit_DialogVisible = false;
                  this.getNowUser();
                });
              resolve();
            });
          }
        }
      );
    }, //
  },
};
</script>

<style  Lang="less" scoped>
.box-card {
  width: 1000px;
  margin: 0 auto;
}
.block {
  position: absolute;
  left: 50%;
  transform: translateX(150%) translateY(-100%);
}
.el-button {
  margin-left: 10px;
  height: 38px;
  text-align: center;
}
.boxdiv {
  margin-bottom: 20px;
  margin-left: 14px;
  font-size: 14px;
  color: #606266;
}
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
.ccc {
  filter: alpha(Opacity=85);
  -moz-opacity: 0.85;
  opacity: 0.85;
}
</style>



