<template>
  <div>
    <!--    -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Reader/Welcome' }"
        ></el-breadcrumb-item
      >
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--    -->
    <el-card class="box-card">
      <!--      -->
      <el-row :gutter="20">
        <!--      spangutter-->
        <el-col :span="8">
          <!--      -->
          <el-input
            placeholder="ID/ID/ID"
            v-model="queryInfo.query"
            @clear="getRecordsList"
            clearable
          >
            <!--            @clear="getRecordsList"-->
            <el-select
              v-model="queryInfo.qparam"
              slot="prepend"
              placeholder=""
            >
              <el-option label="ID" value="1"></el-option>
              <el-option label="ID" value="2"></el-option>
              <el-option label="ID" value="3"></el-option>
              <el-option label="" value="4"></el-option>
            </el-select>
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getRecordsList"
            ></el-button>
            <!--@click="getRecordsList"-->
          </el-input>
        </el-col>
      </el-row>
      <!--        -->

      <el-table
        :data="recordsList"
        :default-sort="{ prop: 'rID', order: 'ascending' }"
        border
        :row-class-name="tableRowClassName"
        ><!---->
        <!--        index-->
        <template slot-scope="scope">
          <el-table-column label="#" type="index"> </el-table-column>
          <el-table-column label="ID" prop="rID" sortable>
          </el-table-column>
          <el-table-column label="" prop="bName"> </el-table-column>
          <el-table-column label="ID" prop="bSingleID" sortable>
          </el-table-column>
          <el-table-column label="ID" prop="uID" sortable>
          </el-table-column>
          <el-table-column label="" prop="applyDate"> </el-table-column>
          <el-table-column label="" prop="borrowDate">
          </el-table-column>
          <el-table-column label="" prop="presetDate">
          </el-table-column>
          <el-table-column label="" prop="returnDate">
          </el-table-column>
          <el-table-column label="" prop="returnCondition">
          </el-table-column>
        </template>
        <!--        -->
        <!--        -->
      </el-table>

      <!--      -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "ReaderRecordList",
  //
  data() {
    return {
      //ID

      queryInfo: {
        query: "", //
        qparam: 1, //
        pagenum: 1, //
        pagesize: 10, //
        uID: "",
      },
      recordsList: [],
      total: 3, //,API
    };
  },
  created() {
    this.getRecordsList();
  }, //*/
  methods: {
    async getRecordsList() {
      var tmpuid = window.sessionStorage.getItem("uID");
      this.queryInfo.uID = JSON.parse(tmpuid);
      await new Promise((resolve) => {
        this.$http
          .post("/users/records/all/uID/", JSON.stringify(this.queryInfo))
          .then((res) => {
            console.log(res);
            if (res.status !== 200)
              this.$message.error("");
            else {
              this.$message.success("");
              console.log(res);
              this.recordsList = [];
              for (let i = 0; i < res.data.userINFO.length; i++) {
                if (res.data.userINFO[i].returnCondition == 0)
                  res.data.userINFO[i].returnCondition = "";
                else if (res.data.userINFO[i].returnCondition == 1)
                  res.data.userINFO[i].returnCondition = "";
                else if (res.data.userINFO[i].returnCondition == 2)
                  res.data.userINFO[i].returnCondition = "";
                else if (res.data.userINFO[i].returnCondition == 3)
                  res.data.userINFO[i].returnCondition = "";

                this.recordsList.push(res.data.userINFO[i]);
              }
              this.total = res.data.total;
            }
          });
        resolve();
      });
    },

    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getRecordsList();
    }, //pagesize
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getRecordsList();
    }, //

    onSortChange({ prop, order }) {
      if (prop === "rID" && order === "ascending") this.queryInfo.qsort = 11;
      else if (prop === "rID" && order === "descending")
        this.queryInfo.qsort = 10;
      else if (prop === "bSingleID" && order === "ascending")
        this.queryInfo.qsort = 21;
      else if (prop === "bSingleID" && order === "descending")
        this.queryInfo.qsort = 20;
      else if (prop === "uID" && order === "ascending")
        this.queryInfo.qsort = 31;
      else if (prop === "uID" && order === "descending")
        this.queryInfo.qsort = 30;
      else this.queryInfo.qsort = 11;

      this.getRecordsList();
      //
    },
    tableRowClassName(obj) {
      if (obj.row.returnCondition === "") {
        return "warning-row";
      } else if (obj.row.returnCondition === "") {
        return "success-row";
      }
      return "";
    },
  },
};
</script>

<style  Lang="less" scoped>
>>> .el-table .warning-row {
  background: oldlace;
}

>>> .el-table .success-row {
  background: #f0f9eb;
}
</style>



