<template>
  <div>
    <!--    -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Reader/Welcome' }"
        ></el-breadcrumb-item
      >
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--    -->
    <el-card class="box-card">
      <!--      -->
      <el-row :gutter="20">
        <!--      spangutter-->
        <el-col :span="8">
          <!--      -->
          <el-input
            placeholder="/ID/"
            v-model="queryInfo.query"
            clearable
          >
            <!--            @clear="getBooksList"-->
            <el-select
              v-model="queryInfo.qparam"
              slot="prepend"
              placeholder=""
            >
              <el-option label="" value="1"></el-option>
              <el-option label="ID" value="2"></el-option>
              <el-option label="" value="3"></el-option>
            </el-select>
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getBooksList"
            ></el-button>
            <!--@click="getBooksList"-->
          </el-input>
        </el-col>
      </el-row>
      <!--        -->
      <el-table :data="booksList" @sort-change="onSortChange" border stripe>
        <!---->
        <!--        index-->
        <el-table-column label="#" type="index"> </el-table-column>
        <el-table-column
          label=""
          prop="bName"
          sortable="custom"
          :sort-orders="['ascending', 'descending']"
        >
        </el-table-column>
        <el-table-column
          label=""
          prop="bID"
          sortable="custom"
          :sort-orders="['ascending', 'descending']"
        >
        </el-table-column>
        <el-table-column label="" prop="bAuthor"> </el-table-column>
        <el-table-column
          label=""
          prop="bYear"
          sortable="custom"
          :sort-orders="['ascending', 'descending']"
        >
        </el-table-column>
        <el-table-column label="" prop="bTotal"></el-table-column>
        <el-table-column
          label=""
          prop="bLeft"
          sortable="custom"
          :sort-orders="['ascending', 'descending']"
        ></el-table-column>
        <!--        -->
        <!--        -->

        <el-table-column label="" class="orderRow">
          <template slot-scope="scope">
            <!-- -->
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editUserBorrowInfo(scope.row.bID)"
              >
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--      -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--    -->
    <!--    -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="40%"
      @close="editDialogClosed"
    >
      <!--     -->
      <el-form
        :model="editForm"
        :rules="editRules"
        ref="editFormRef"
        label-width="120px"
      >
        <el-form-item label="ID" prop="bID">
          <el-input v-model="editForm.bID" disabled></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="uID">
          <el-input v-model="editForm.uID" disabled></el-input>
        </el-form-item>
      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserBorrowInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "ReaderBookList",
  //
  data() {
    var checkbID = (rule, value, callback) => {
      const regbID = /^[0-9]{8}$/;
      if (regbID.test(value)) return callback();
      else callback(new Error("ID"));
    };

    return {
      //ID

      queryInfo: {
        query: "", //
        qparam: 1, //
        qsort: 11, //
        pagenum: 1, //
        pagesize: 10, //
      },
      booksList: [],
      total: 3, //,API

      editDialogVisible: false, //
      editForm: {
        bID: "",
        uID: "",
        returnCondition: "",
      },
      editRules: {
        bName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 1,
            max: 20,
            message: " 1  20 ",
            trigger: "blur",
          },
        ],
        bID: [
          { required: true, message: "ID", trigger: "blur" },
          { validator: checkbID, trigger: "blur" },
        ],
        uName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 1,
            max: 20,
            message: " 1  20 ",
            trigger: "blur",
          },
        ],
        uID: [{ required: true, message: "ID", trigger: "blur" }],
      },
      //
    };
  },
  created() {
    this.getBooksList();
  }, //*/
  methods: {
    async getBooksList() {
      await new Promise((resolve) => {
        this.$http
          .post("/users/booklist/", JSON.stringify(this.queryInfo))
          .then((res) => {
            console.log(res);
            if (res.status !== 200)
              return this.$message.error("");
            else {
              this.$message.success("");
              console.log(res);
              this.booksList = [];
              for (let i = 0; i < res.data.bookInfo.length; i++)
                this.booksList.push(res.data.bookInfo[i]);
              this.total = res.data.total;
            }
          });
        resolve();
      });
    },
    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getBooksList();
    }, //pagesize
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getBooksList();
    }, //
    onSortChange({ prop, order }) {
      if (prop === "bID" && order === "ascending") this.queryInfo.qsort = 11;
      else if (prop === "bID" && order === "descending")
        this.queryInfo.qsort = 10;
      else if (prop === "bName" && order === "ascending")
        this.queryInfo.qsort = 21;
      else if (prop === "bName" && order === "descending")
        this.queryInfo.qsort = 20;
      else if (prop === "bYear" && order === "ascending")
        this.queryInfo.qsort = 31;
      else if (prop === "bYear" && order === "descending")
        this.queryInfo.qsort = 30;
      else if (prop === "bLeft" && order === "ascending")
        this.queryInfo.qsort = 41;
      else if (prop === "bLeft" && order === "descending")
        this.queryInfo.qsort = 40;
      else this.queryInfo.qsort = 11;

      this.getBooksList();
      //
    },

    async showEditDialog(bName, bID, bAuthor, bYear, bTotal, bLeft) {
      //bIDscope.row.bID

      console.log(bID);
      this.editForm.bName = bName;
      this.editForm.bID = bID;
      this.editForm.bAuthor = bAuthor;
      this.editForm.bYear = bYear;
      this.editForm.bTotal = bTotal;
      this.editForm.bLeft = bLeft;
      this.editDialogVisible = true;
      this.editForm.uName = JSON.parse(window.sessionStorage.getItem("uName"));
      this.editForm.uID = JSON.parse(window.sessionStorage.getItem("uID"));
    }, //
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    }, //
    async editUserBorrowInfo(bID) {
      var tmpuid = window.sessionStorage.getItem("uID");
      this.editForm.uID = JSON.parse(tmpuid);
      this.editForm.bID = bID;
      await new Promise((resolve) => {
        this.$http
          .post("users/records/borrow/uID/", JSON.stringify(this.editForm))
          .then((res) => {
            //addForm
            //rID
            console.log(res);
            if (res.data.success === false)
              this.$message.error(res.data.message);
            else this.$message.success("");

            this.addDialogVisible = false;
            this.getBooksList(); //
          });
        resolve();
      });
    }, //
  },
};
</script>

<style  Lang="less" scoped>
</style>



