<template>
  <div>
    <!--    -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--    -->
    <el-card class="box-card">
      <!--      -->
      <el-row :gutter="20">
        <!--      spangutter-->
        <el-col :span="8">
          <!--      -->
          <el-input
            placeholder="ID/ID/ID"
            v-model="queryInfo.query"
            @clear="getRecordsList"
            clearable
          >
            <!--            @clear="getRecordsList"-->
            <el-select
              v-model="queryInfo.qparam"
              slot="prepend"
              placeholder=""
            >
              <el-option label="ID" value="1"></el-option>
              <el-option label="ID" value="2"></el-option>
              <el-option label="ID" value="3"></el-option>
              <el-option label="" value="4"></el-option>
            </el-select>
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getRecordsList"
            ></el-button>
            <!--@click="getRecordsList"-->
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--        -->
      <el-table
        :data="recordsList"
        @sort-change="onSortChange"
        border
        :row-class-name="tableRowClassName"
        ><!---->
        <!--        index-->
        <el-table-column label="#" type="index"> </el-table-column>
        <el-table-column
          label="ID"
          prop="rID"
          sortable="custom"
          :sort-orders="['ascending', 'descending']"
        >
        </el-table-column>
        <el-table-column label="" prop="bName"> </el-table-column>
        <el-table-column
          label="ID"
          prop="bSingleID"
          sortable="custom"
          :sort-orders="['ascending', 'descending']"
        >
        </el-table-column>
        <el-table-column
          label="ID"
          prop="uID"
          sortable="custom"
          :sort-orders="['ascending', 'descending']"
        >
        </el-table-column>
        <el-table-column label="" prop="borrowDate"> </el-table-column>
        <el-table-column label="" prop="presetDate">
        </el-table-column>
        <el-table-column label="" prop="returnDate"> </el-table-column>
        <el-table-column label="" prop="returnCondition">
        </el-table-column>

        <!--        -->
        <!--        -->

        <el-table-column label="/" class="orderRow">
          <template slot-scope="scope">
            <!-- -->
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="
                  editRecordInfo(scope.row.rID, scope.row.returnCondition)
                "
              >
              </el-button>
            </el-tooltip>

            <!-- -->
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="
                  removeRecordByrID(scope.row.rID, scope.row.returnCondition)
                "
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--      -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--    -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="40%"
      @close="addDialogClosed"
    >
      <!--     -->
      <el-form
        :model="addForm"
        :rules="addRules"
        ref="addFormRef"
        label-width="120px"
      >
        <el-form-item label="ID" prop="bID">
          <el-input v-model="addForm.bID"></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="bSingleID">
          <el-input v-model="addForm.bSingleID"></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="uID">
          <el-input v-model="addForm.uID"></el-input>
        </el-form-item>
      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRecord"> </el-button>
      </span>
    </el-dialog>

    <!--    -->
    <!--    -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="40%">
      <!---->
      <el-form
        :model="editForm"
        :rules="editRules"
        ref="editFormRef"
        label-width="120px"
      >
        <el-form-item label="ID" prop="rID">
          <el-input v-model="editForm.rID" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="bName">
          <el-input v-model="editForm.bName" disabled></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="bSingleID">
          <el-input v-model="editForm.bSingleID" disabled></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="uID">
          <el-input v-model="editForm.uID" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="borrowDate">
          <el-input v-model="editForm.borrowDate" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="presetDate">
          <el-input v-model="editForm.presetDate" disabled></el-input>
        </el-form-item>
      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false" @close="editDialogClosed"
          > </el-button
        >
        <el-button type="primary" @click="editRecordInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "RecordList",
  //
  data() {
    var checkbID = (rule, value, callback) => {
      const regbID = /^[0-9]{8}$/;
      if (regbID.test(value)) return callback();
      else callback(new Error("ID8"));
    };
    var checkbSingleID = (rule, value, callback) => {
      const regbSingleID = /^[0-9]{11}$/;
      if (regbSingleID.test(value)) return callback();
      else callback(new Error("ID11"));
    };
    var checkuID = (rule, value, callback) => {
      const reguID = /^[1-2][0-9]{7}$/;
      if (reguID.test(value)) return callback();
      else callback(new Error("/"));
    };

    return {
      //ID

      queryInfo: {
        query: "", //
        qparam: 1, //
        qsort: 11, //
        pagenum: 1, //
        pagesize: 10, //
      },
      recordsList: [],
      total: 3, //,API

      addDialogVisible: false, //
      addForm: {
        bSingleID: "",
        uID: "",
      },
      addRules: {
        bID: [
          { required: true, message: "8ID", trigger: "blur" },
          { validator: checkbID, trigger: "blur" },
        ],
        bSingleID: [
          {
            required: true,
            message: "11ID",
            trigger: "blur",
          },
          { validator: checkbSingleID, trigger: "blur" },
        ],
        uID: [
          { required: true, message: "/", trigger: "blur" },
          { validator: checkuID, trigger: "blur" },
        ],
      },
      //

      editCondition: "",
      editDialogVisible: false, //,rID
      editForm: {
        rID: "",
        bName: "",
        bSingleID: "",
        uID: "",
        borrowDate: "",
        presetDate: "",
        returnDate: "",
      },
      editRules: {
        rID: [{ required: true }],
        bName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 1,
            max: 20,
            message: " 1  20 ",
            trigger: "blur",
          },
        ],
        bSingleID: [
          { required: true, message: "ID", trigger: "blur" },
          { validator: checkbSingleID, trigger: "blur" },
        ],
        uID: [
          { required: true, message: "/", trigger: "blur" },
          { validator: checkuID, trigger: "blur" },
        ],
        borrowDate: [
          { required: true, message: "", trigger: "blur" },
        ],
        presetDate: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        returnDate: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
      //
    };
  },
  created() {
    this.getRecordsList();
  }, //
  methods: {
    async getRecordsList() {
      await new Promise((resolve) => {
        this.$http
          .post("/records/", JSON.stringify(this.queryInfo))
          .then((res) => {
            console.log(res);
            if (res.status !== 200)
              this.$message.error("");
            else {
              this.$message.success("");
              console.log(res);
              this.recordsList = [];
              for (let i = 0; i < res.data.userINFO.length; i++) {
                if (res.data.userINFO[i].returnCondition == 0)
                  res.data.userINFO[i].returnCondition = "";
                else if (res.data.userINFO[i].returnCondition == 1)
                  res.data.userINFO[i].returnCondition = "";
                else if (res.data.userINFO[i].returnCondition == 2)
                  res.data.userINFO[i].returnCondition = "";
                else if (res.data.userINFO[i].returnCondition == 3)
                  res.data.userINFO[i].returnCondition = "";

                this.recordsList.push(res.data.userINFO[i]);
              }
              this.total = res.data.total;
            }
          });
        resolve();
      });
    },
    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getRecordsList();
    }, //pagesize
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getRecordsList();
    }, //
    onSortChange({ prop, order }) {
      if (prop === "rID" && order === "ascending") this.queryInfo.qsort = 11;
      else if (prop === "rID" && order === "descending")
        this.queryInfo.qsort = 10;
      else if (prop === "bSingleID" && order === "ascending")
        this.queryInfo.qsort = 21;
      else if (prop === "bSingleID" && order === "descending")
        this.queryInfo.qsort = 20;
      else if (prop === "uID" && order === "ascending")
        this.queryInfo.qsort = 31;
      else if (prop === "uID" && order === "descending")
        this.queryInfo.qsort = 30;
      else this.queryInfo.qsort = 11;

      this.getRecordsList();
      //
    },

    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    }, //
    async addRecord() {
      this.$refs.addFormRef.validate(
        //aync
        async (valid) => {
          if (!valid) return;
          //
          else {
            await new Promise((resolve) => {
              this.$http
                .post("/records/addnow/", JSON.stringify(this.addForm))
                .then((res) => {
                  //addForm
                  //rID
                  console.log(res);
                  if (res.data.success === false)
                    this.$message.error(res.data.message);
                  else this.$message.success("");

                  this.addDialogVisible = false;
                  this.getRecordsList(); //
                });
              resolve();
            });
          }
        }
      );
    }, //

    async showEditDialog(
      rID,
      bName,
      bSingleID,
      uID,
      borrowDate,
      presetDate,
      returnDate,
      returnCondition
    ) {
      //rIDscope.row.rID
      console.log(rID);
      this.editForm.rID = rID;
      this.editForm.bSingleID = bSingleID;
      this.editForm.bName = bName;
      this.editForm.uID = uID;
      this.editForm.borrowDate = borrowDate;
      this.editForm.presetDate = presetDate;
      this.editForm.returnDate = returnDate;
      this.editForm.returnCondition = returnCondition;
      this.editCondition = returnCondition;

      if (returnCondition === "" || returnCondition === "")
        this.editDialogVisible = true;
      else {
        this.editDialogVisible = false;
        alert("");
      }
    }, //
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    }, //
    async editRecordInfo(rID, returnCondition) {
      if (returnCondition === "") {
        alert("");
        return;
      }
      await new Promise((resolve) => {
        this.$http
          .post("/records/rID/", JSON.stringify({ rID }))
          .then((res) => {
            if (res.data.success === false) this.$message.error("");
            else this.$message.success("");
            this.editDialogVisible = false;
            this.getRecordsList(); //
          });
        resolve();
      });
    },

    async removeRecordByrID(rID, rCondition) {
      //
      console.log(rID);
      if (rCondition !== "") {
        alert("");
        return;
      }

      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => {
        return err;
      });

      if (confirmResult !== "confirm")
        return this.$message.info("");

      await new Promise((resolve) => {
        this.$http
          .post("/records/del/", JSON.stringify({ rID }))
          .then((res) => {
            if (res.status !== 200) this.$message.error("");
            else this.$message.success("");
            this.getRecordsList();

            //
          });
      });
    }, //rID

    tableRowClassName(obj) {
      if (obj.row.returnCondition === "") {
        return "warning-row";
      } else if (obj.row.returnCondition === "") {
        return "success-row";
      }
      return "";
    },
  },
};
</script>

<style  Lang="less" scoped>
>>> .el-table .warning-row {
  background: oldlace;
}
>>> .el-table .success-row {
  background: #f0f9eb;
}
.el-table .success-row :hover {
  background: red;
}
.el-tooltip.el-button {
  margin-right: 0px !important;
}
</style>



