<template>
  <div>
    <!--    -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--    -->
    <el-card class="box-card">
      <!--      -->
      <el-row :gutter="20">
        <!--      spangutter-->
        <el-col :span="8">
          <!--      -->
          <el-input placeholder="/" v-model="queryInfo.query"  @clear="getBuyBooksList" clearable >
            <!--            @clear="getBuyBooksList"-->
            <el-select v-model="queryInfo.qparam" slot="prepend" placeholder="" >
              <el-option label="" value="1" ></el-option>
              <el-option label="" value="2" ></el-option>
            </el-select>
            <el-button slot="append" icon="el-icon-search" @click="getBuyBooksList"></el-button>
            <!--@click="getBuyBooksList"-->
          </el-input>
        </el-col>

      </el-row>
      <!--        -->
      <el-table :data="buyBooksList"  border stripe><!---->
        <!--        index-->
        <el-table-column label="#" type="index"> </el-table-column>
        <el-table-column label="ID" prop="bID"> </el-table-column>
        <el-table-column label="" prop="bName"> </el-table-column>
        <el-table-column label="" prop="bAuthor"> </el-table-column>
        <el-table-column label="" prop="bYear"> </el-table-column>
        <el-table-column label="" prop="bTotal"></el-table-column>
        <el-table-column label="" prop="bReason" ></el-table-column>
        <!--        -->
        <!--        -->
        <el-table-column label="" class="orderRow">
          <template slot-scope="scope">
            <!-- -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeBuyBookBybID(scope.row.bID)"></el-button>
            </el-tooltip>


          </template>
        </el-table-column>
      </el-table>

      <!--      -->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>

    </el-card>




  </div>
</template>

<script>
export default {
  name: "BuyBookList",
  //
  data(){

    return {
      //ID

      queryInfo: {
        query: '', //
        qparam: 1,//
        pagenum: 1,//
        pagesize: 10,//
      },
      buyBooksList: [],
      total: 3,//,API



    }

  },
  created(){
    this.getBuyBooksList();
  },//*/
  methods:{

    async getBuyBooksList() {
      await new Promise(resolve => {
        this.$http.post('/operator/buyBooks/', JSON.stringify(this.queryInfo)).then(res => {
          console.log(res)
          if (res.status !== 200)
            this.$message.error('')
          else {
            this.$message.success('')
            console.log(res);
            this.buyBooksList = [];
            for (let i = 0; i < res.data.recordInfo.length; i++)
              this.buyBooksList.push(res.data.recordInfo[i]);

            this.total = res.data.total;

          }
        })
        resolve();
      })

    },
    //
    handleSizeChange(newSize){
      this.queryInfo.pagesize=newSize;
      //this.getBooksList();
    },//pagesize
    handleCurrentChange(newPage){
      this.queryInfo.pagenum=newPage;
      //this.getBooksList();
    },//
    async removeBuyBookBybID(bID){
      //
      const confirmResult=await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err=>{return err})

      if(confirmResult!=='confirm')
        return this.$message.info('')

      console.log(bID)
      await new Promise(resolve => {
        this.$http.post('/operator/buyBooks/bID/', JSON.stringify({bID})).then(res => {
          console.log(res);
          if (res.data.success === false)
            this.$message.error('')
          else
            this.$message.success('')
          this.getBuyBooksList()

          //
        })
      })

    }, //bID




  }

}
</script>

<style  Lang="scss" scoped>
.el-tooltip.el-button{
  margin-left:40px;
}
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}

</style>



