<template>
  <div>
    <!--    -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--    -->
    <el-card class="box-card">
      <!--      -->
      <el-row :gutter="20">
        <!--      spangutter-->
        <el-col :span="8">
          <!--      -->
          <el-input
            placeholder="ID/ID/ID"
            v-model="queryInfo.query"
            @click="getBorrowList"
            clearable
          >
            <!--            @clear="getBorrowList"-->
            <el-select
              v-model="queryInfo.qparam"
              slot="prepend"
              placeholder=""
            >
              <el-option label="ID" value="1"></el-option>
              <el-option label="ID" value="2"></el-option>
              <el-option label="ID" value="3"></el-option>
              <el-option label="" value="4"></el-option>
            </el-select>
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getBorrowList"
            ></el-button>
            <!--@click="getBorrowList"-->
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--        -->
      <el-table :data="borrowList" @sort-change="onSortChange" border stripe
        ><!---->
        <!--        index-->
        <el-table-column label="#" type="index"> </el-table-column>
        <el-table-column
          label="ID"
          prop="rID"
          sortable="custom"
          :sort-orders="['ascending', 'descending']"
        >
        </el-table-column>
        <el-table-column label="" prop="bName"> </el-table-column>
        <el-table-column
          label="ID"
          prop="bSingleID"
          sortable="custom"
          :sort-orders="['ascending', 'descending']"
        >
        </el-table-column>
        <el-table-column
          label="ID"
          prop="uID"
          sortable="custom"
          :sort-orders="['ascending', 'descending']"
        >
        </el-table-column>
        <el-table-column label="" prop="applyDate"> </el-table-column>
        <el-table-column label="" prop="returnCondition">
        </el-table-column>

        <!--        -->
        <!--        -->

        <el-table-column label="/" class="orderRow">
          <template slot-scope="scope">
            <!-- -->
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editBorrowInfo(scope.row.rID)"
              >
              </el-button>
            </el-tooltip>

            <!-- -->
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="removeBorrowByrID(scope.row.rID)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--      -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--    -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="40%"
      @close="addDialogClosed"
    >
      <!--     -->
      <el-form
        :model="addForm"
        :rules="addRules"
        ref="addFormRef"
        label-width="120px"
      >
        <el-form-item label="ID" prop="bID">
          <el-input v-model="addForm.bID"></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="bSingleID">
          <el-input v-model="addForm.bSingleID"></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="uID">
          <el-input v-model="addForm.uID"></el-input>
        </el-form-item>
      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRecord"> </el-button>
      </span>
    </el-dialog>

    <!--    -->
    <!--    -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="40%">
      <!---->
      <el-form
        :model="editForm"
        :rules="editRules"
        ref="editFormRef"
        label-width="120px"
      >
        <el-form-item label="ID" prop="rID">
          <el-input v-model="editForm.rID" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="bName">
          <el-input v-model="editForm.bName" disabled></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="bSingleID">
          <el-input v-model="editForm.bSingleID" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="uName">
          <el-input v-model="editForm.uName" disabled></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="uID">
          <el-input v-model="editForm.uID" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="applyDate">
          <el-input v-model="editForm.applyDate" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="borrowDate">
          <el-col :span="11">
            <el-date-picker
              type="date"
              placeholder=""
              v-model="editForm.borrowDate"
              style="width: 100%"
            >
            </el-date-picker>
          </el-col>
        </el-form-item>
      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false" @close="editDialogClosed"
          > </el-button
        >
        <el-button type="primary" @click="editBorrowInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "BorrowList",
  //
  data() {
    var checkbSingleID = (rule, value, callback) => {
      const regbSingleID = /^[0-9]{11}$/;
      if (regbSingleID.test(value)) return callback();
      else callback(new Error("ID11"));
    };
    var checkbID = (rule, value, callback) => {
      const regbID = /^[0-9]{8}$/;
      if (regbID.test(value)) return callback();
      else callback(new Error("ID8"));
    };
    var checkuID = (rule, value, callback) => {
      const reguID = /^[1-2][0-9]{7}$/;
      if (reguID.test(value)) return callback();
      else callback(new Error("/"));
    };

    return {
      //ID

      queryInfo: {
        query: "", //
        qsort: 11, //
        qparam: 1, //
        pagenum: 1, //
        pagesize: 10, //
      },
      borrowList: [],
      total: 3, //,API

      addDialogVisible: false, //
      addForm: {
        bID: "",
        bSingleID: "",
        uID: "",
      },
      addRules: {
        bID: [
          { required: true, message: "8ID", trigger: "blur" },
          { validator: checkbID, trigger: "blur" },
        ],
        bSingleID: [
          {
            required: true,
            message: "11ID",
            trigger: "blur",
          },
          { validator: checkbSingleID, trigger: "blur" },
        ],
        uID: [
          { required: true, message: "/", trigger: "blur" },
          { validator: checkuID, trigger: "blur" },
        ],
      },
      //

      editDialogVisible: false, //,aID
      editForm: {
        rID: "",
        bName: "",
        bSingleID: "",
        uName: "",
        uID: "",
        applyDate: "",
        borrowDate: "",
        returnCondition: "",
      },
      editRules: {
        rID: [{ required: true }],
        bName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 1,
            max: 20,
            message: " 1  20 ",
            trigger: "blur",
          },
        ],
        bSingleID: [
          { required: true, message: "ID", trigger: "blur" },
          { validator: checkbSingleID, trigger: "blur" },
        ],
        uName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 10,
            message: " 2  10 ",
            trigger: "blur",
          },
        ],
        uID: [
          { required: true, message: "/", trigger: "blur" },
          { validator: checkuID, trigger: "blur" },
        ],
        borrowDate: [
          { required: true, message: "", trigger: "blur" },
        ],
        applyDate: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      //
    };
  },
  created() {
    this.getBorrowList();
  }, //*/
  methods: {
    async getBorrowList() {
      await new Promise((resolve) => {
        this.$http
          .post("/borrow/", JSON.stringify(this.queryInfo))
          .then((res) => {
            console.log(res);
            if (res.status !== 200)
              this.$message.error("");
            else {
              this.$message.success("");
              this.borrowList = [];
              for (let i = 0; i < res.data.userInfo.length; i++) {
                if (res.data.userInfo[i].returnCondition == 0)
                  res.data.userInfo[i].returnCondition = "";
                else if (res.data.userInfo[i].returnCondition == 1)
                  res.data.userInfo[i].returnCondition = "";
                else if (res.data.userInfo[i].returnCondition == 2)
                  res.data.userInfo[i].returnCondition = "";
                else if (res.data.userInfo[i].returnCondition == 3)
                  res.data.userInfo[i].returnCondition = "";

                this.borrowList.push(res.data.userInfo[i]);
              }
              this.total = res.data.total;
            }
          });
        resolve();
      });
    },

    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getBorrowList();
    }, //pagesize
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getBorrowList();
    }, //
    onSortChange({ prop, order }) {
      if (prop === "rID" && order === "ascending") this.queryInfo.qsort = 11;
      else if (prop === "rID" && order === "descending")
        this.queryInfo.qsort = 10;
      else if (prop === "bSingleID" && order === "ascending")
        this.queryInfo.qsort = 21;
      else if (prop === "bSingleID" && order === "descending")
        this.queryInfo.qsort = 20;
      else if (prop === "uID" && order === "ascending")
        this.queryInfo.qsort = 31;
      else if (prop === "uID" && order === "descending")
        this.queryInfo.qsort = 30;
      else this.queryInfo.qsort = 11;

      this.getBorrowList();
      //
    },

    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    }, //
    async addRecord() {
      this.$refs.addFormRef.validate(
        //aync
        async (valid) => {
          if (!valid) return;
          //
          else {
            await new Promise((resolve) => {
              this.$http
                .post("/records/addnow/", JSON.stringify(this.addForm))
                .then((res) => {
                  //addForm
                  //rID
                  console.log(res);
                  if (res.data.success === false)
                    this.$message.error(res.data.message);
                  else this.$message.success("");

                  this.addDialogVisible = false;
                  this.getBorrowList(); //
                });
              resolve();
            });
          }
        }
      );
    }, //

    async editBorrowInfo(rID) {
      console.log(rID);
      await new Promise((resolve) => {
        this.$http.post("/borrow/rID/", JSON.stringify({ rID })).then((res) => {
          if (res.data.success === false) this.$message.error(res.data.message);
          else this.$message.success("");
          this.editDialogVisible = false;
          this.getBorrowList(); //
        });
        resolve();
      });
      this.getBorrowList();
    },

    async removeBorrowByrID(rID) {
      //
      console.log(rID);
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => {
        return err;
      });

      if (confirmResult !== "confirm")
        return this.$message.info("");

      await new Promise((resolve) => {
        this.$http
          .post("/records/del/", JSON.stringify({ rID }))
          .then((res) => {
            if (res.status !== 200) this.$message.error("");
            else this.$message.success("");
            this.getBorrowList();

            //
          });
      });
    }, //rID
  },
};
</script>

<style  Lang="less" scoped>
</style>



