<template>
  <div>
    <!--    -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--    -->
    <el-card class="box-card">
      <!--      -->
      <el-row :gutter="20">
        <!--      spangutter-->
        <el-col :span="8">
          <!--      -->
          <el-input placeholder="/ID/" v-model="queryInfo.query"  @clear="getBooksList" clearable >
            <!--            @clear="getBooksList"-->
            <el-select v-model="queryInfo.qparam" slot="prepend" placeholder="" >
              <el-option label="" value="1" ></el-option>
              <el-option label="ID" value="2"></el-option>
              <el-option label="" value="3" ></el-option>
            </el-select>
            <el-button slot="append" @click="getBooksList" icon="el-icon-search"></el-button>
            <!--@click="getBooksList"-->
          </el-input>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" @click="addDialogVisible=true"></el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" @click="deleteDialogVisible=true"></el-button>
        </el-col>
      </el-row>
      <!--        -->
      <el-table :data="booksList"  @sort-change="onSortChange" border stripe >
        <!---->
        <!--        index-->
        <el-table-column label="#" type="index"> </el-table-column>
        <el-table-column label="" prop="bName"  sortable="custom" :sort-orders="['ascending','descending']"> </el-table-column>
        <el-table-column label="" prop="bID" sortable="custom" :sort-orders="['ascending','descending']"> </el-table-column>
        <el-table-column label="" prop="bAuthor"> </el-table-column>
        <el-table-column label="" prop="bYear"  sortable="custom" :sort-orders="['ascending','descending']"> </el-table-column>
        <el-table-column label="" prop="bTotal"></el-table-column>
        <el-table-column label="" prop="bLeft"  sortable="custom" :sort-orders="['ascending','descending']"></el-table-column>
        <!--        -->
        <!--        -->

        <el-table-column label="/" class="orderRow">
          <template slot-scope="scope">
            <!-- -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog
              (scope.row.bName,scope.row.bID,scope.row.bAuthor,scope.row.bYear,
              scope.row.bTotal)">
              </el-button>
            </el-tooltip>

            <!-- -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeBookBybID(scope.row.bID)"></el-button>
            </el-tooltip>


          </template>
        </el-table-column>
      </el-table>
      <!--      -->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
    </el-card>
    <!--    -->
    <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="40%"
        @close="addDialogClosed">

      <!--     -->
      <el-form :model="addForm" :rules="addRules" ref="addFormRef" label-width="120px">
        <el-form-item label="" prop="bName">
          <el-input v-model="addForm.bName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bAuthor">
          <el-input v-model="addForm.bAuthor"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bYear">
          <el-input v-model="addForm.bYear"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bTotal">
          <el-input v-model="addForm.bTotal"></el-input>
        </el-form-item>

      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addBook"> </el-button>
  </span>
    </el-dialog>

    <el-dialog
        title=""
        :visible.sync="deleteDialogVisible"
        width="40%"
        @close="deleteDialogClosed">

      <!--     -->
      <el-form :model="deleteForm" :rules="deleteRules" ref="deleteFormRef" label-width="120px">
        <el-form-item label="ID" prop="bID">
          <el-input v-model="deleteForm.bID"></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="bSingleID">
          <el-input v-model="deleteForm.bSingleID"></el-input>
        </el-form-item>
      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
    <el-button @click="deleteDialogVisible = false"> </el-button>
    <el-button type="primary" @click="deleteBook()"> </el-button>
  </span>
    </el-dialog>

    <!--    -->
    <!--    -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="40%">
      <!---->
      <el-form :model="editForm" :rules="editRules" ref="editFormRef" label-width="120px">
        <el-form-item label="ID" prop="bID">
          <el-input v-model="editForm.bID" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="bName">
          <el-input v-model="editForm.bName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bAuthor">
          <el-input v-model="editForm.bAuthor"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bYear">
          <el-input v-model="editForm.bYear"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bTotal">
          <el-input v-model="editForm.bTotal" ></el-input>
        </el-form-item>

      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false" @close="editDialogClosed"> </el-button>
    <el-button type="primary" @click="editBookInfo"> </el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "BookList",
  //
  data(){
    var checkbID=(rule,value,callback)=>{
      const regbID=/^[0-9]{8}$/
      if(regbID.test(value))
        return callback()
      else
        callback(new Error('ID8'))
    }
    var checkbSingleID=(rule,value,callback)=>{
      const regbSingleID=/^[0-9]{11}$/
      if(regbSingleID.test(value))
        return callback()
      else
        callback(new Error('ID11'))
    }
    var checkbYear=(rule,value,callback)=>{
      const regbYear=/^[1-2][0-9]{3}$/
      if(regbYear.test(value))
        return callback()
      else
        callback(new Error(''))
    }
    var checkbTotal = (rule, value, callback) => {
      const regbTotal = /^[0-9]{1,3}$/
      if (regbTotal.test(value))
        return callback()
      else
        callback(new Error(','))
    };
    var checkbLeft=(rule,value,callback)=>{
      const regbLeft=/^[0-9]{1,3}$/
      if(regbLeft.test(value))
        return callback()
      else
        callback(new Error(''))
    };
    var check_bTotal = (rule, value, callback) => {
      const regbTotal = /^[0-9]{1,3}$/
      if(parseInt(value)<this.editTmp)
        callback(new Error(''))
      if (regbTotal.test(value))
        return callback()
      else
        callback(new Error(''))
    };
    return{
      //ID

      queryInfo: {
        query: '', //
        qparam:1,//
        qsort:11,//
        pagenum: 1,//
        pagesize: 10,//
      },
      booksList:[],
      total:3,//,API



      addDialogVisible:false,//
      addForm: {
        bName:"",
        bAuthor:"",
        bYear:"",
        bTotal:0,
        bLeft:0
      },
      addRules: {
        bName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' }
        ],
        bAuthor: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 20, message: ' 2  20 ', trigger: 'blur' }
        ],
        bYear: [
          { required: true, message: '', trigger: 'blur' },
          {validator:checkbYear,trigger:'blur'}
        ],
        bTotal: [
          { required: true, message: '', trigger: 'blur' },
          {validator:checkbTotal,trigger:'blur'}
        ],
        bLeft: [
          { required: true, message: ',', trigger: 'blur' },
          {validator:checkbLeft,trigger:'blur'}
        ],

      },
      //


      editDialogVisible:false,//
      editForm: {
        bName:"",
        bID:"",
        bAuthor:"",
        bYear:2000,
        bTotal:0,
        bLeft:0
      },
      editTmp:0,
      editRules: {
        bName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' }
        ],
        bID: [
          { required: true, message: '8ID', trigger: 'blur' },
          {validator:checkbID,trigger:'blur'}
        ],
        bAuthor: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 20, message: ' 2  20 ', trigger: 'blur' }
        ],
        bYear: [
          { required: true, message: '', trigger: 'blur' },
          {validator:checkbYear,trigger:'blur'}
        ],
        bTotal: [
          { required: true, message: '', trigger: 'blur' },
          {validator:check_bTotal,trigger:'blur'}
        ],


      },
      //

      deleteDialogVisible:false,//
      deleteForm: {
        bID:"",
        bSingleID:"",
      },
      deleteRules: {

        bID: [
          { required: true, message: '8ID', trigger: 'blur' },
          {validator:checkbID,trigger:'blur'}
        ],
        bSingleID: [
          { required: true, message: '11ID', trigger: 'blur' },
          {validator:checkbSingleID,trigger:'blur'}
        ],

      },
      //
    }




  },
  created(){
    this.getBooksList();
  },//*/
  methods:{


    async getBooksList(){
      await new Promise((resolve)=>{
        this.$http.post('/users/booklist/', JSON.stringify(this.queryInfo)).then(res=>{
            console.log(res);
            if(res.status!==200)
                return this.$message.error('')
              else
              {
                this.$message.success('')
                console.log(res);
                this.booksList=[];
                for(let i=0; i<res.data.bookInfo.length; i++)
                  this.booksList.push(res.data.bookInfo[i]);
                this.total=res.data.total;

              }

        })
        resolve();
      })



      },
    //
    handleSizeChange(newSize){
      this.queryInfo.pagesize=newSize;
      this.getBooksList();
    },//pagesize
    handleCurrentChange(newPage){
      this.queryInfo.pagenum=newPage;
      this.getBooksList();
    },//

    onSortChange({ prop, order }) {
      if(prop==='bID'&&order==='ascending')
        this.queryInfo.qsort=11;
      else if(prop==='bID'&&order==='descending')
        this.queryInfo.qsort=10;
      else if(prop==='bName'&&order==='ascending')
        this.queryInfo.qsort=21;
      else if(prop==='bName'&&order==='descending')
        this.queryInfo.qsort=20;
      else if(prop==='bYear'&&order==='ascending')
        this.queryInfo.qsort=31;
      else if(prop==='bYear'&&order==='descending')
        this.queryInfo.qsort=30;
      else if(prop==='bLeft'&&order==='ascending')
        this.queryInfo.qsort=41;
      else if(prop==='bLeft'&&order==='descending')
        this.queryInfo.qsort=40;
      else
        this.queryInfo.qsort=11;

      this.getBooksList();
      //
    },







    addDialogClosed(){
      this.$refs.addFormRef.resetFields()
    },//
    addBook(){
      this.$refs.addFormRef.validate(async valid=>{
            if(!valid)return
            //
            else
            {
              console.log(valid)
              await new Promise(resolve=>{
                this.$http.post('/books/',JSON.stringify(this.addForm)).then(res=>{
                  console.log(res);
                  if(res.success===false)
                    this.$message.error(res.message)
                  else
                    this.$message.success('')

                  this.addDialogVisible = false
                  this.getBooksList()//
                })//addForm
                resolve();
              })


            }

          })
    },//


    async showEditDialog(bName,bID,bAuthor,bYear,bTotal){
      //bIDscope.row.bID
      console.log(bID)
      this.editForm.bName=bName
      this.editForm.bID=bID
      this.editForm.bAuthor=bAuthor
      this.editForm.bYear=bYear
      this.editForm.bTotal=bTotal
      this.editTmp=bTotal
      this.editDialogVisible=true

    },//
    editDialogClosed(){
      this.$refs.editFormRef.resetFields()
    },//
    editBookInfo(){
      this.$refs.editFormRef.validate(//
          async valid=>{
            if(!valid)return
            //
            else
            {
              console.log(valid)
              await new Promise(resolve => {
                this.$http.post('/books/bID/',JSON.stringify(this.editForm)).then(res=>{
                  if(res.success===false)
                    return this.$message.error(res.message)
                  else
                    this.$message.success('')
                  this.editDialogVisible = false
                  this.getBooksList()//
                })
                resolve();
              })


            }

          })

    },//

    async removeBookBybID(bID){
      //
      console.log(bID)
      const confirmResult=await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err=>{return err})

      if(confirmResult!=='confirm')
        return this.$message.info('')

       await new Promise(resolve=>{
         this.$http.post('/books/del_bID/',JSON.stringify({bID})).then(res=>{
           if(res.success===false)
              this.$message.error(res.message)
           else
              this.$message.success('')
           this.getBooksList()
         })
         resolve();
       })



      //

    }, //bID

    deleteDialogClosed(){
      this.$refs.deleteFormRef.resetFields()
    },//
    async deleteBook(){
      this.$refs.deleteFormRef.validate(
        async valid=>{
            if(!valid)return
            //
            else
            {
              console.log(valid)
              const confirmResult=await this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
              }).catch(err=>{
                console.log('')
                return err})

              if(confirmResult!=='confirm')
                return this.$message.info('')

              console.log(this.deleteForm.bID,this.deleteForm.bSingleID)

              await new Promise(resolve=>{
                this.$http.post('/books/bID/bSingleID/', JSON.stringify(this.deleteForm)).then(res=>{
                  if(res.success===false)
                     this.$message.error(res.message)
                  else
                    this.$message.success('')
                  this.deleteDialogVisible = false
                  this.getBooksList()//
                })
                resolve();
              })

            }

          })
    },//
/*
    removeBookBybSingleID(bID,bSingleID){
      //
      const confirmResult=await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err=>{
        console.log('why?')
        return err})

      if(confirmResult!=='confirm')
        return this.$message.info('')

      console.log(bID,bSingleID)

      const {data:res}=await this.$http.post('/books/bID/bSingleID/', {bID,bSingleID})
       if(res.success===false)
         return this.$message.error(res.message)

       this.$message.success('')
       this.getBooksList()

      //

    }, //bIDbSingleID
*/



  }

}
</script>

<style  Lang="less" scoped>


</style>



