<template>
  <div>
    <!--    -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--    -->
    <el-card class="box-card">
      <!--      -->
      <el-row :gutter="20">
        <!--      spangutter-->
        <el-col :span="8">
          <!--      -->
          <el-input
            placeholder="/"
            v-model="queryInfo.query"
            @clear="getUsersList"
            clearable
          >
            <!--            @clear="getUsersList"-->
            <el-select
              v-model="queryInfo.qparam"
              slot="prepend"
              placeholder=""
            >
              <el-option label="" value="1"></el-option>
              <el-option label="/" value="2"></el-option>
            </el-select>
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUsersList"
            ></el-button>
            <!--@click="getUsersList"-->
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--        -->
      <el-table :data="usersList" border stripe
        ><!---->
        <!--        index-->
        <el-table-column label="#" type="index"> </el-table-column>
        <el-table-column label="" prop="uName"> </el-table-column>
        <el-table-column label="/" prop="uID"> </el-table-column>
        <el-table-column label="" prop="uPhone"> </el-table-column>
        <el-table-column label="" prop="uCharacter"> </el-table-column>
        <!--        -->
        <el-table-column label="/" class="orderRow">
          <template slot-scope="scope">
            <!-- -->
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="
                  showEditDialog(
                    scope.row.uName,
                    scope.row.uID,
                    scope.row.uPhone,
                    scope.row.uCharacter
                  )
                "
              ></el-button>
            </el-tooltip>

            <!-- -->
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="removeUserByuID(scope.row.uID)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--      -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--    -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="40%"
      @close="addDialogClosed"
    >
      <!--     -->
      <el-form
        :model="addForm"
        :rules="addRules"
        ref="addFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="uName">
          <el-input v-model="addForm.uName"></el-input>
        </el-form-item>

        <el-form-item label="/" prop="uID">
          <el-input v-model="addForm.uID"></el-input>
        </el-form-item>

        <el-form-item label="" prop="uPhone">
          <el-input v-model="addForm.uPhone"></el-input>
        </el-form-item>

        <el-form-item label="" prop="uPWD">
          <el-input v-model="addForm.uPWD"></el-input>
        </el-form-item>
      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>

    <!--    -->
    <!--    -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="40%"
      :before-close="handleClose"
    >
      <!---->
      <el-form
        :model="editForm"
        :rules="editRules"
        ref="editFormRef"
        label-width="100px"
      >
        <el-form-item label="">
          <el-input v-model="editForm.uName" disabled></el-input>
        </el-form-item>
        <el-form-item label="/">
          <el-input v-model="editForm.uID" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.uCharacter" disabled></el-input>
        </el-form-item>

        <el-form-item label="" prop="uPhone">
          <el-input v-model="editForm.uPhone"></el-input>
        </el-form-item>
      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false" @close="editDialogClosed"
          > </el-button
        >
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Users",
  //
  data() {
    var checkuID = (rule, value, callback) => {
      const reguID = /^[1-2][0-9]{7}$/;
      if (reguID.test(value)) return callback();
      else callback(new Error("/"));
    };
    var checkuPhone = (rule, value, callback) => {
      const regPhone = /^1[3-9][0-9]{9}$/;
      if (regPhone.test(value)) return callback();
      else callback(new Error(""));
    };

    return {
      ///

      queryInfo: {
        query: "", //
        qparam: 1, //
        pagenum: 1, //
        pagesize: 10, //
      },
      usersList: [],
      total: 4, //,API

      addDialogVisible: false, //
      addForm: {
        uName: "",
        uID: "",
        uPhone: "",
        uCharacter: "",
        uPWD: "",
      },
      addRules: {
        uName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 10,
            message: " 2  10 ",
            trigger: "blur",
          },
        ],
        uID: [
          { required: true, message: "/", trigger: "blur" },
          { validator: checkuID, trigger: "blur" },
        ],
        uPhone: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkuPhone, trigger: "blur" },
        ],
        uPWD: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 15,
            message: " 6  15 ",
            trigger: "blur",
          },
        ],
      },
      //

      editDialogVisible: false, //
      editForm: {
        uName: "",
        uID: "",
        uPhone: "",
        uCharacter: "",
      },
      editRules: {
        uPhone: [
          {
            required: true,
            message: "",
            trigger: ["blur", "change"],
          },
          { validator: checkuPhone, trigger: "blur" },
        ],
      },
      //
    };
  },
  created() {
    this.getUsersList();
  }, //*/
  methods: {
    async getUsersList() {
      await new Promise((resolve) => {
        this.$http
          .post("/users/", JSON.stringify(this.queryInfo))
          .then((res) => {
            console.log(res);
            if (res.status !== 200)
              return this.$message.error("");
            else {
              this.$message.success("");
              console.log(res);
              this.usersList = [];
              for (let i = 0; i < res.data.userInfo.length; i++) {
                if (!res.data.userInfo[i].uCharacter) {
                  res.data.userInfo[i].uCharacter = "";
                  this.usersList.push(res.data.userInfo[i]);
                }
              }
              this.total = res.data.total;
            }
          });
        resolve();
      });
    },
    //
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getUsersList();
    }, //pagesize
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getUsersList();
    }, //

    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    }, //
    async addUser() {
      this.$refs.addFormRef.validate(
        //aync
        async (valid) => {
          if (!valid) return;
          //
          else {
            this.addForm.uCharacter = "";
            await new Promise((resolve) => {
              this.$http
                .post("/operator/addReader/", JSON.stringify(this.addForm))
                .then((res) => {
                  if (res.success === false) this.$message.error(res.message);
                  else this.$message.success("");

                  this.addDialogVisible = false;
                  this.getUsersList(); //
                });
              resolve();
            });
            //addForm
          }
        }
      );
    }, //

    showEditDialog(uName, uID, uPhone, uCharacter) {
      //uIDscope.row.uID
      console.log(uID);
      this.editForm.uName = uName;
      this.editForm.uID = uID;
      this.editForm.uPhone = uPhone;
      this.editForm.uCharacter = uCharacter;

      this.editDialogVisible = true;
    }, //
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    }, //
    async editUserInfo() {
      this.$refs.editFormRef.validate(
        //aync
        async (valid) => {
          if (!valid) return;
          //
          else {
            console.log(valid);
            await new Promise((resolve) => {
              this.$http
                .post("/users/uID/", JSON.stringify(this.editForm))
                .then((res) => {
                  if (res.success === false)
                    this.$message.error("");
                  else this.$message.success("");
                  this.editDialogVisible = false;
                  this.getUsersList(); //
                });
              resolve();
            });
          }
        }
      );
    }, //

    async removeUserByuID(uID) {
      //
      console.log(uID);
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => {
        return err;
      });

      if (confirmResult !== "confirm")
        return this.$message.info("");

      await new Promise((resolve) => {
        this.$http
          .post("/operator/delReader/", JSON.stringify({ uID }))
          .then((res) => {
            if (res.success === false) this.$message.error(res.message);
            else this.$message.success("");
            this.getUsersList();

            //
          });
      });
    }, //uID
  },
};
</script>

<style  Lang="less" scoped>
.el-tooltip.el-button {
  margin-right: 40px;
}
</style>




